import './App.css';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
import { Navbar } from './Navbar';
import { Home } from './Home';
import { Fea1 } from './Fea1';
import { Fea2 } from './Fea2';

function App() {
  return (
    <Router>
      <div className="App">
        <h1>Router demo</h1>
        <Navbar />
        <Routes>
          <Route path="/home" element={<Home />} />
          <Route path="/fea1" element={<Fea1 />} />
          <Route path="/fea2" element={<Fea2 />} />
        </Routes>
        <p>Hello world of routes</p>
      </div>
    </Router>
  );
}

export default App;
